.grid {
  box-sizing: border-box;
  flex-flow: wrap;
  flex: 0 auto;
  margin: 0 -.5em;
  display: flex;
}

.grid.grid-nogutter {
  margin: 0;
}

.grid.grid-nogutter > .col {
  padding: 0;
}

.col {
  box-sizing: border-box;
  flex: 1 0 0;
  min-width: 0;
  max-width: 100%;
  padding: 0 .5em;
}

@media only screen and (width <= 480px) {
  .col.col-sm {
    flex: 100%;
    max-width: 100%;
  }
}

@media only screen and (width <= 1024px) {
  .col.col-md {
    flex: 100%;
    max-width: 100%;
  }
}

@media only screen and (width <= 1360px) {
  .col.col-lg {
    flex: 100%;
    max-width: 100%;
  }
}

.col-align-top {
  align-self: flex-start;
}

.col-align-bottom {
  align-self: flex-end;
}

.col-align-middle {
  align-self: center;
}

.col-top, .col-bottom, .col-middle {
  flex-direction: column;
  display: flex;
}

.col-top {
  justify-content: flex-start !important;
}

.col-bottom {
  justify-content: flex-end !important;
}

.col-middle {
  justify-content: center;
}

.grid-start {
  justify-content: flex-start;
}

.grid-center {
  justify-content: center;
}

.grid-end {
  justify-content: flex-end;
}

.grid-around {
  justify-content: space-around;
}

.grid-between {
  justify-content: space-between;
}

.col-first {
  order: -1;
}

.col-last {
  order: 1;
}

.grid-reverse {
  flex-direction: row-reverse;
}

.col-fixed {
  flex: initial;
}

